.header {
  @include section-wrap(100%);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  @include bg-img("../img/hero@1x.jpg");
  background-size: cover;
  background-position: center center;
  height: 700px;

  @media (min-resolution: $retina-dppx) {
    background-image: url("../img/hero@2x.jpg");
    background-size: cover;
    background-position: center center;
    width: 100%;
    height: 700px;
  }

  // &__navigation,
  .header__navigation,
  .navigation {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    padding-top: 28px;
    align-items: baseline;

    .side {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 100%;
      padding: 0;
      list-style-type: none;

      &__item {
        &:hover {
          transition: all 0.2s ease-in-out;
          transform: scale(1.2);
        }
      }
      &__link,
      .link {
        @include font(16px, 500, 19px, $text-light, center);

        &:hover {
          border-top: 1px solid $shadow-link;
          color: $shadow-link;
        }
        &:active {
          color: $text-link;
          transition: all 0.2s ease-out;
        }
      }
    }
    .navigation__side--desktop-width {
      width: 65%;
    }

    &__logo {
      margin: 0 89px 0 98px;
      align-self: center;
    }
    &__accent-btn,
    .accent-btn {
      @include button(15px, 15px, $bg-green);
      margin-left: 38px;
      display: flex;
      height: 32px;

      &__icon-white-heart {
        margin-left: 9px;
      }
    }
  }

  // .page-header__block-hero,
  &__block-hero,
  .block-hero {
    // margin-top: 198px;

    &__head {
      margin-top: 198px;
      @include font(36px, 700, 40px, $text-light, center);
    }
    &__btn-wrapper {
      margin-top: 24px;
      .accent-btn {
        @include button(29px, 29px, $bg-green);
      }
      .accent-btn__light {
        @include button(24px, 24px, $bg-btn-light);
        margin-left: 43px;
      }
    }
  }
  .accent-btn:hover {
    @include button-hover-active($btn-hover);
  }
  .accent-btn:active {
    @include button-hover-active($btn-active);
    transform: scale(0.8);
  }
}

